import { createGame, startGame, joinGame } from "../util/net";

const Home = () => (
    <>
        <h1>Jammin'</h1>

        <button onClick={() => createGame()}>Create Game</button>
        
        <form onSubmit={(e) => {e.preventDefault(); joinGame(document.getElementById('gameid').value)}}>
            <label for="gameid">Join game</label>
            <input type="text" name="gameid" id="gameid" />
            <input type="submit" />
        </form>
        
        <button onClick={() => startGame()}>Start Game</button>
    </>
);
export default Home;