import { io } from "socket.io-client";
import { createRef } from 'preact';
import { route } from 'preact-router';

// Hacky way(for now) to get editor
export const editorRef = createRef();

const socket = io();

socket.onAny((msg) => console.log(msg));

socket.on('create-game-res', (id) => {
    alert(`Your game ID is: ${id}`);
});
socket.on('start-round', (tracks) => {
    route('/editor');
    setTimeout(() => {
        // Wait for routing(hacky)
        console.log(editorRef.current)
        editorRef.current?.setState((prev) => ({
            ...prev,
            tracks,
        }));
    }, 1000);
});

socket.on('error', (msg) => {
    alert(msg);
})

export const createGame = () => {
    socket.emit('create-game');
}

export const joinGame = (id) => {
    console.log(id);
    socket.emit('join-game', id)
}

export const startGame = () => {
    socket.emit('start-game');
}

export const submitTrack = (track) => {
    socket.emit('submit-track', track);
}